<template>
  <section class="card">
    <div class="card-header">
      <div class="utils__title">
        <strong>Add a Customer</strong>
      </div>
    </div>
    <div class="card-body">
      <div :class="$style.addPost">
        <a-form class="mt-3" :form="form" @submit="handleSubmit">
          <div class="form-group">
            <a-form-item label="Customer Name">
              <a-input v-decorator="['customer_name',{ rules: [{ required: true, message: 'Please customer name is importamnt' }]}]" placeholder="Customer Name"/>
            </a-form-item>
            <a-form-item label="Business Name">
              <a-input v-decorator="['business_name',{ rules: [{ required: true, message: 'Please business name is important' }]}]" placeholder="Business Name"/>
            </a-form-item>
            <a-form-item label="Phone Number">
              <a-input v-decorator="['phone',{ rules: [{ required: true, message: 'Please phone number is important' }]}]" placeholder="Phone Number"/>
            </a-form-item>
            <a-form-item label="Email Address">
              <a-input v-decorator="['email',{ rules: [{type: 'email', message: 'the input is not a valid E-mail'}]}]" placeholder="Email Address"/>
            </a-form-item>
          </div>
          <div class="form-group">
            <div class="form-group">
              <a-form-item label="Customer Location">
                <a-textarea v-decorator="['location',{ rules: [{ required: false }]}]" placeholder="Customer Location" />
              </a-form-item>
            </div>
            <a-form-item>
              <div :class="$style.submit">
                <span class="mr-3">
                  <a-button type="primary" htmlType="submit">Save and Post</a-button>
                </span>
                <a-button @click="handleReset()" type="danger">Discard</a-button>
              </div>
            </a-form-item>
          </div>
        </a-form>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'index',
  data() {
    return {
      form: this.$form.createForm(this),
    }
  },
  methods: {
    handleReset() {
      this.form.resetFields()
    },
    handleSubmit(e) {
      e.preventDefault()
      this.form.validateFields((err, values) => {
        if (!err) {
          this.$nprogress.start()
          const requestPayload = {
            business_name: values.business_name,
            full_name: values.customer_name,
            phone: values.phone,
            email: values.email,
            address: values.location,
          }

          this.axios.post('clients/store', requestPayload)
            .then(response => {
              this.handleReset()
              this.$nprogress.done()
              // console.log('stats',response.status)
              if (response.status === 200) {
                this.$notification['success']({
                  message: 'Customer Registration',
                  description: 'You have successfully Created a Customer ',
                })
              }
            })
            .catch(error => {
              // console.log('error', error.response.data.data)
              if (error.response.status === 401) {
                this.$notification['warning']({
                  message: 'Customer Registration',
                  description: 'Sorry you have a validation error :: ' + error.response.data.data.phone,
                })
              } else {
                this.$notification['warning']({
                  message: 'Customer Registration',
                  description: 'Sorry an error occurred.',
                })
              }
            })
        }
      })
    },
    handleChange(info) {
      const status = info.file.status
      if (status !== 'uploading') {
        console.log(info.file, info.fileList)
      }
      if (status === 'done') {
        this.$message.success(`${info.file.name} file uploaded successfully.`)
      } else if (status === 'error') {
        this.$message.error(`${info.file.name} file upload failed.`)
      }
    },
  },
}
</script>

<style lang="scss" module>
  @import "./style.module.scss";
</style>
